import React from 'react'
import { BrowserRouter as Router, Route, NavLink } from 'react-router-dom'
import { TunesScreen, SetlistScreen, RandomScreen } from './components'
import { debounce, findIndex } from 'lodash'
import classNames from 'classnames'
import tunesJson from './tunes'
import './App.css'

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      tunes: tunesJson,
      setlist: [],
      random: {},
      menuIsHidden: true,
      sortBy: 'title',
      filters: ['jazz-standards'],
      activeRoute: '/tunes',
      tunesScrollPosition: 0,
      setlistScrollPosition: 0
    }
    this.toggleSelected = this.toggleSelected.bind(this)
    this.getNewRandomTune = this.getNewRandomTune.bind(this)
  }

  componentDidMount() {
    // Get tunes from API
    // Uncomment in production
    // ----------------------------------------------------
    // fetch('https://thomas-mcevoy.com/api/tunes/')
    //   .then(response => JSON.parse(response))
    //   .then(responseJson => this.setState({
    //     tunes: responseJson
    //   }))
    //   .catch(error => console.log(error))

    this.getNewRandomTune()
    
    window.addEventListener('scroll', debounce((e) => {
      if (window.location.pathname === '/tunes')
        this.setState({tunesScrollPosition: window.scrollY})
      else if (window.location.pathname === '/setlist')
        this.setState({setlistScrollPosition: window.scrollY})
    }, 100))
  }

  render() {
    return (
      <Router>
        <div className="App">
          <header className="App-header">
            <div className="Router">
              <NavLink className="Router-link" to="/tunes">Tunes</NavLink>
              <NavLink className="Router-link" to="/setlist">Setlist</NavLink>
              <NavLink className="Router-link" to="/random">Random</NavLink>
            </div>
            <div
              className={classNames({
                'Menu-button': true,
                'active': !this.state.menuIsHidden
              })}
              onClick={() => {
                this.setState((prevState, props) => {
                  return { menuIsHidden: !prevState.menuIsHidden }
                })
              }}
            >
              <svg viewBox="0 0 38 26" xmlns="http://www.w3.org/2000/svg">
                <path d="M36,4H2A2,2,0,0,1,2,0H36A2,2,0,0,1,36,4Z"/>
                <path d="M36,15H2a2,2,0,0,1,0-4H36A2,2,0,0,1,36,15Z"/>
                <path d="M36,26H2a2,2,0,0,1,0-4H36A2,2,0,0,1,36,26Z"/>
              </svg>
            </div>
          </header>
          <div className={classNames({
            'Menu-dropdown': true,
            'hidden': this.state.menuIsHidden
          })}>
            <div className="Submenu-sort">
              <div className="Submenu-title">SORT BY</div>
              <div className="Submenu-contents">
                <div
                  className={classNames({
                    'Submenu-item': true,
                    'active': this.state.sortBy === 'title'
                  })}
                  onClick={() => this.sortBy('title')}
                >Title</div>
                <div
                  className={classNames({
                    'Submenu-item': true,
                    'active': this.state.sortBy === 'composer'
                  })}
                  onClick={() => this.sortBy('composer')}
                >Composer</div>
                <div
                  className={classNames({
                    'Submenu-item': true,
                    'active': this.state.sortBy === 'year'
                  })}
                  onClick={() => this.sortBy('year')}
                >Year</div>
              </div>
            </div>
            <div className="Submenu-filter">
              <div className="Submenu-title">FILTERS</div>
              <div className="Submenu-contents">
                <div
                  className={classNames({
                    'Submenu-item': true,
                    'active': this.state.filters.indexOf('jazz-standards') > -1
                  })}
                  onClick={() => this.toggleFilter('jazz-standards')}
                >Jazz Standards</div>
                <div
                  className={classNames({
                    'Submenu-item': true,
                    'active': this.state.filters.indexOf('american-songbook') > -1
                  })}
                  onClick={() => this.toggleFilter('american-songbook')}>American Songbook</div>
                <div
                  className={classNames({
                    'Submenu-item': true,
                    'active': this.state.filters.indexOf('blues') > -1
                  })}
                  onClick={() => this.toggleFilter('blues')}>Blues</div>
                <div
                  className={classNames({
                    'Submenu-item': true,
                    'active': this.state.filters.indexOf('rhythm') > -1
                  })}
                  onClick={() => this.toggleFilter('rhythm')}>Rhythm changes</div>
                <div
                  className={classNames({
                    'Submenu-item': true,
                    'active': this.state.filters.indexOf('latin') > -1
                  })}
                  onClick={() => this.toggleFilter('latin')}>Latin</div>
                <div
                  className={classNames({
                    'Submenu-item': true,
                    'active': this.state.filters.indexOf('waltz') > -1
                  })}
                  onClick={() => this.toggleFilter('waltz')}>Waltzes</div>
                <div
                  className={classNames({
                    'Submenu-item': true,
                    'active': this.state.filters.indexOf('holiday') > -1
                  })}
                  onClick={() => this.toggleFilter('holiday')}>Holiday tunes</div>
                <div
                  className={classNames({
                    'Submenu-item': true,
                    'active': this.state.filters === ['jazz-standards', 'american-songbook', 'blues', 'rhythm', 'latin', 'waltz', 'holiday']
                  })}
                  onClick={() => this.setState({filters: ['jazz-standards', 'american-songbook', 'blues', 'rhythm', 'latin', 'waltz', 'holiday']})}>All</div>
              </div>
            </div>
          </div>
          <main className="App-main">
            <Route path="/tunes" render={() =>
              <TunesScreen
                tunes={this.state.tunes.filter(tune => {
                  let i = 0
                  let showTune = false
                  while (i < this.state.filters.length && showTune === false) {
                    switch(this.state.filters[i]) {
                      case 'jazz-standards':
                        if (tune.composer && (
                            tune.composer.includes('Charlie Parker') ||
                            tune.composer.includes('Dizzy Gillespie') ||
                            tune.composer.includes('Thelonious Monk') ||
                            tune.composer.includes('Horace Silver') ||
                            tune.composer.includes('Joe Henderson') ||
                            tune.composer.includes('Kenny Dorham') ||
                            tune.composer.includes('Oscar Pettiford'))) {
                              showTune = true
                            }
                        break
                      case 'american-songbook':
                        if (tune.composer && (
                            tune.composer.includes('Jerome Kern') ||
                            tune.composer.includes('Cole Porter') ||
                            tune.composer.includes('Irving Berlin') ||
                            tune.composer.includes('Richard Rodgers') ||
                            tune.composer.includes('George Gershwin') ||
                            tune.composer.includes('Harold Arlen'))) {
                              showTune = true
                            }
                        break
                      case 'blues':
                        if (tune.form && tune.form === 'blues') {
                          showTune = true
                        }
                        break
                      case 'rhythm':
                        if (tune.form && tune.form === 'rhythm') {
                          showTune = true
                        }
                        break
                      case 'latin':
                        if (tune.type && tune.type === 'latin') {
                          showTune = true
                        }
                        break
                      case 'waltz':
                        if (tune.type && tune.type === 'waltz') {
                          showTune = true
                        }
                        break
                      case 'holiday':
                        if (tune.type && tune.type === 'holiday') {
                          showTune = true
                        }
                        break
                      default:
                        showTune = false
                    }
                    i++
                  }
                  return showTune
                })}
                toggleSelected={this.toggleSelected}
                scrollPosition={this.state.tunesScrollPosition}
              />
            }/>
            <Route path="/setlist" render={() =>
              <SetlistScreen
                setlist={this.state.setlist}
                toggleSelected={this.toggleSelected}
                scrollPosition={this.state.setlistScrollPosition}
              />
            }/>
            <Route path="/random" render={() => 
              <RandomScreen
                random={this.state.random}
                getNewRandomTune={this.getNewRandomTune}
              />
            }/>
          </main>
        </div>
      </Router>
    )
  }

  getNewRandomTune() {
    const { tunes } = this.state
    this.setState({random: tunes[Math.floor(Math.random() * tunes.length)]})
  }

  sortBy(str) {
    this.setState((prevState, props) => {
      let tunes = [...prevState.tunes]
      return {
        sortBy: str,
        tunes: tunes.sort((a, b) => {
          if (str === 'year') {
            return a.year - b.year
          }
          else {
            const strA = a[str]
            const strB = b[str]
            if (strA < strB) return -1
            if (strA > strB) return 1
            return 0
          }
        })
      }
    })
  }

  toggleFilter(str) {
    const index = this.state.filters.indexOf(str)
    if (index === -1) {
      this.setState((prevState, props) => {
        return { filters: [...prevState.filters, str] }
      })
    }
    else {
      this.setState((prevState, props) => {
        return { filters: prevState.filters.filter((_, i) => i !== index) }
      })
    }
  }

  toggleSelected(tune) {
    this.setState((prevState, props) => {
      return {
        tunes: this.getNextTunes(tune, prevState.tunes),
        setlist: this.getNextSetlist(tune, prevState.setlist)
      }
    })
  }

  getNextTunes(tune, prevTunes) {
    let nextTunes = [...prevTunes]
    const index = findIndex(prevTunes, {title: tune.title})
    let updatedTune = Object.assign({}, prevTunes[index])
    updatedTune.selected = updatedTune.hasOwnProperty('selected') ? 
      !updatedTune.selected : true
    nextTunes[index] = updatedTune
    return nextTunes
  }

  getNextSetlist(tune, prevSetlist) {
    let nextSetlist = [...prevSetlist]
    const index = findIndex(prevSetlist, {title: tune.title})
    if (index === -1) nextSetlist.push(tune)
    else nextSetlist.splice(index, 1)
    return nextSetlist
  }
}

export default App



